:root {
  --spanish-gray: #999;
  --sonic-silver: #787878;
  --eerie-black: #212121;
  --salmon-pink: #ff8f9c;
  --sandy-brown: #f6a355;
  --bittersweet: #f66;
  --ocean-green: #46c389;
  --davys-gray: #545454;
  --cultured: #ededed;
  --white: #fff;
  --onyx: #454545;
  --fs-1: 1.563rem;
  --fs-2: 1.375rem;
  --fs-3: 1.25rem;
  --fs-4: 1.125rem;
  --fs-5: 1rem;
  --fs-6: .938rem;
  --fs-7: .875rem;
  --fs-8: .813rem;
  --fs-9: .75rem;
  --fs-10: .688rem;
  --fs-11: .625rem;
  --weight-300: 300;
  --weight-400: 400;
  --weight-500: 500;
  --weight-600: 600;
  --weight-700: 700;
  --border-radius-md: 10px;
  --border-radius-sm: 5px;
  --transition-timing: .2s ease;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

button {
  font: inherit;
  cursor: pointer;
  background: none;
  border: none;
}

img, ion-icon, button, a {
  display: block;
}

span {
  display: inline-block;
}

html {
  overscroll-behavior: contain;
  font-family: Poppins, sans-serif;
}

input {
  width: 100%;
  font: inherit;
  display: block;
}

input::placeholder {
  font: inherit;
}

body {
  background: var(--white);
}

body::-webkit-scrollbar {
  width: 15px;
}

body::-webkit-scrollbar-track {
  background: var(--white);
  border-left: 1px solid var(--cultured);
}

body::-webkit-scrollbar-thumb {
  border: 3px solid var(--white);
  background: #ccc;
  border-radius: 10px;
}

body::-webkit-scrollbar-thumb:hover {
  background: #b3b3b3;
}

.container {
  padding: 0 15px;
}

.has-scrollbar {
  padding-bottom: 5px;
}

.has-scrollbar::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}

.has-scrollbar::-webkit-scrollbar-thumb {
  border: 3px solid var(--white);
  background: none;
  border-radius: 20px;
}

.has-scrollbar:hover::-webkit-scrollbar-thumb {
  background: #e6e6e6;
}

.has-scrollbar::-webkit-scrollbar-thumb:hover {
  background: #ccc;
}

.title {
  color: var(--eerie-black);
  font-size: var(--fs-5);
  font-weight: var(--weight-600);
  letter-spacing: .4px;
  text-transform: capitalize;
  border-bottom: 1px solid var(--cultured);
  margin-bottom: 30px;
  padding-bottom: 10px;
}

.overlay {
  width: 100%;
  height: 100vh;
  opacity: 0;
  pointer-events: none;
  z-index: 15;
  background: #00000080;
  transition: all .5s;
  position: fixed;
  top: 0;
  left: 0;
}

.overlay.active {
  opacity: 1;
  pointer-events: all;
}

.modal {
  width: 100%;
  height: 100vh;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 10;
  background: #00000080;
  justify-content: center;
  align-items: center;
  animation: 1s ease-in-out 5s forwards popup;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

@keyframes popup {
  0% {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }

  100% {
    opacity: 1;
    visibility: visible;
    pointer-events: all;
  }
}

.modal.closed {
  display: none;
}

.modal-close-overlay {
  width: 100%;
  height: 100%;
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
}

.newsletter-img {
  display: none;
}

.modal-content {
  max-width: 350px;
  background: var(--white);
  border-radius: var(--border-radius-md);
  z-index: 2;
  margin: 20px;
  animation: .5s ease-in-out 5s forwards scaleUp;
  position: relative;
  overflow: hidden;
}

@keyframes scaleUp {
  0% {
    transform: scale(.9);
  }

  100% {
    transform: scale(1);
  }
}

.modal-close-btn {
  background: var(--salmon-pink);
  color: var(--white);
  border-radius: var(--border-radius-sm);
  padding: 5px;
  font-size: 16px;
  position: absolute;
  top: 15px;
  right: 15px;
}

.modal-close-btn:hover {
  opacity: .9;
}

.modal-close-btn ion-icon {
  --ionicon-stroke-width: 70px;
}

.newsletter {
  text-align: center;
  padding: 50px 30px;
}

.newsletter-header {
  margin-bottom: 20px;
}

.newsletter-title {
  color: var(--onyx);
  font-size: var(--fs-2);
  font-weight: var(--weight-600);
  margin-bottom: 10px;
}

.newsletter-desc {
  color: var(--sonic-silver);
  font-size: var(--fs-7);
  line-height: 1.6;
}

.email-field {
  font-size: var(--fs-7);
  border-radius: var(--border-radius-sm);
  border: 1px solid var(--cultured);
  margin-bottom: 16px;
  padding: 8px 16px;
}

.btn-newsletter {
  background: var(--eerie-black);
  color: var(--white);
  font-size: var(--fs-7);
  font-weight: var(--weight-600);
  text-transform: uppercase;
  border-radius: var(--border-radius-sm);
  transition: var(--transition-timing);
  margin: auto;
  padding: 10px 15px;
}

.btn-newsletter:hover {
  background: var(--salmon-pink);
}

.notification-toast {
  background: var(--white);
  max-width: 300px;
  border-radius: var(--border-radius-md);
  z-index: 5;
  align-items: flex-start;
  gap: 15px;
  padding: 15px;
  transition: all .5s ease-in-out;
  animation: 10s ease-in-out infinite slideInOut;
  display: flex;
  position: fixed;
  bottom: 80px;
  left: 20px;
  right: 20px;
  transform: translateX(calc(-100% - 40px));
  box-shadow: 0 5px 20px #00000026;
}

@keyframes slideInOut {
  0%, 45%, 100% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(calc(-100% - 40px));
  }

  50%, 95% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
}

.notification-toast.closed {
  display: none;
}

.toast-close-btn {
  color: var(--sonic-silver);
  position: absolute;
  top: 10px;
  right: 10px;
}

.toast-close-btn ion-icon {
  --ionicon-stroke-width: 50px;
}

.toast-banner {
  width: 70px;
  height: 70px;
  border: 1px solid var(--cultured);
  border-radius: var(--border-radius-sm);
}

.toast-banner img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
}

.toast-detail {
  width: calc(100% - 85px);
  padding-right: 10px;
}

.toast-message {
  font-size: var(--fs-10);
  color: var(--sonic-silver);
  margin-bottom: 8px;
}

.toast-title {
  font-size: var(--fs-7);
  font-weight: var(--weight-500);
  color: var(--onyx);
}

.toast-meta {
  font-size: var(--fs-10);
  color: var(--sonic-silver);
}

.header-top, .header-user-actions, .desktop-navigation-menu {
  display: none;
}

.header-main {
  border-bottom: 1px solid var(--cultured);
  padding: 20px 0;
}

.header-logo {
  margin-bottom: 20px;
}

.header-logo img {
  margin: auto;
}

.header-search-container {
  position: relative;
}

.header-search-container .search-field {
  font-size: var(--fs-7);
  color: var(--onyx);
  border: 1px solid var(--cultured);
  border-radius: var(--border-radius-md);
  padding: 10px 50px 10px 15px;
}

.search-field::-webkit-search-cancel-button {
  display: none;
}

.search-btn {
  background: var(--white);
  color: var(--onyx);
  border-radius: var(--border-radius-md);
  transition: color var(--transition-timing);
  padding: 8px 15px;
  font-size: 18px;
  position: absolute;
  top: 50%;
  right: 2px;
  transform: translateY(-50%);
}

.search-btn:hover {
  color: var(--salmon-pink);
}

.mobile-bottom-navigation {
  background: var(--white);
  width: 100%;
  max-width: 500px;
  z-index: 5;
  justify-content: space-around;
  align-items: center;
  margin: auto;
  padding: 5px 0;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 0 10px #00000040;
}

.mobile-bottom-navigation .action-btn {
  color: var(--eerie-black);
  padding: 10px;
  font-size: 26px;
  position: relative;
}

.mobile-bottom-navigation .count {
  background: var(--bittersweet);
  color: var(--white);
  font-size: 12px;
  font-weight: var(--weight-500);
  border-radius: 20px;
  padding: 2px 4px;
  line-height: 1;
  position: absolute;
  top: 0;
  right: 0;
}

.mobile-navigation-menu {
  background: var(--white);
  width: 100%;
  max-width: 320px;
  height: 100vh;
  overscroll-behavior: contain;
  visibility: hidden;
  z-index: 20;
  padding: 20px;
  transition: all .5s;
  position: fixed;
  top: 0;
  left: -100%;
  overflow-y: scroll;
  box-shadow: 0 0 10px #0000001a;
}

.mobile-navigation-menu.active {
  visibility: visible;
  left: 0;
}

.menu-top {
  border-bottom: 2px solid var(--cultured);
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding-bottom: 15px;
  display: flex;
}

.menu-top .menu-title {
  color: var(--salmon-pink);
  font-size: var(--fs-4);
  font-weight: var(--weight-600);
}

.menu-close-btn {
  color: var(--eerie-black);
  font-size: 22px;
}

.menu-close-btn ion-icon {
  --ionicon-stroke-width: 50px;
}

.mobile-menu-category-list {
  margin-bottom: 30px;
}

.menu-category .accordion-menu {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.mobile-menu-category-list .menu-category {
  border-bottom: 1px solid var(--cultured);
}

.mobile-menu-category-list .menu-title {
  color: var(--onyx);
  font-size: var(--fs-6);
  font-weight: var(--weight-500);
  padding: 12px 0;
}

.accordion-menu > div {
  font-size: 14px;
}

.accordion-menu ion-icon {
  color: var(--onyx);
  --ionicon-stroke-width: 90px;
}

.accordion-menu.active .add-icon, .accordion-menu .remove-icon {
  display: none;
}

.accordion-menu .add-icon, .accordion-menu.active .remove-icon {
  display: block;
}

.menu-category .submenu-category-list {
  margin-left: 10px;
}

.submenu-title {
  font-size: var(--fs-6);
  color: var(--sonic-silver);
  font-weight: var(--weight-300);
  padding: 6px 0;
}

.submenu-title:hover {
  color: var(--davys-gray);
}

.submenu-category-list {
  max-height: 0;
  visibility: hidden;
  transition: all .5s ease-in-out;
  overflow: hidden;
}

.submenu-category-list.active {
  max-height: 148px;
  visibility: visible;
}

.menu-bottom .menu-category-list {
  margin-bottom: 20px;
}

.menu-bottom .menu-category {
  border-bottom: none;
}

.menu-bottom .menu-title {
  font-size: var(--fs-6);
  font-weight: var(--weight-500);
  color: var(--eerie-black);
  padding: 12px 0;
}

.accordion-menu.active .caret-back {
  transform: rotate(-.25turn);
}

.menu-bottom .submenu-category-list {
  border: 1px solid var(--cultured);
  border-radius: var(--border-radius-md);
  margin-left: 0;
  padding: 0 15px;
  box-shadow: 0 0 10px #0000000d;
}

.menu-bottom .submenu-category:not(:last-child) {
  border-bottom: 1px solid var(--cultured);
}

.menu-social-container {
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.menu-social-container .social-link {
  background: var(--cultured);
  color: var(--eerie-black);
  border-radius: var(--border-radius-md);
  padding: 10px;
  font-size: 20px;
}

.banner {
  margin: 30px 0;
}

.slider-container {
  border-radius: var(--border-radius-md);
  scroll-snap-type: inline mandatory;
  overscroll-behavior-inline: contain;
  align-items: center;
  gap: 10px;
  display: flex;
  overflow-x: auto;
  overflow-y: hidden;
}

.slider-item {
  min-width: 100%;
  max-height: 450px;
  aspect-ratio: 1 / 1;
  border-radius: var(--border-radius-md);
  scroll-snap-align: start;
  position: relative;
  overflow: hidden;
}

.slider-item .banner-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: right;
}

.banner-content {
  border-radius: var(--border-radius-md);
  background: #fffc;
  padding: 20px 25px;
  position: absolute;
  bottom: 25px;
  left: 25px;
  right: 25px;
}

.banner-subtitle {
  color: var(--salmon-pink);
  font-size: var(--fs-7);
  font-weight: var(--weight-500);
  text-transform: capitalize;
  letter-spacing: 2px;
  margin-bottom: 10px;
}

.banner-title {
  color: var(--eerie-black);
  font-size: var(--fs-1);
  text-transform: uppercase;
  margin-bottom: 10px;
  line-height: 1;
}

.banner-text {
  display: none;
}

.banner-btn {
  background: var(--salmon-pink);
  color: var(--white);
  width: max-content;
  font-size: var(--fs-11);
  font-weight: var(--weight-600);
  text-transform: uppercase;
  border-radius: var(--border-radius-sm);
  transition: var(--transition-timing);
  padding: 4px 10px;
}

.banner-btn:hover {
  background: var(--eerie-black);
}

.category {
  margin-bottom: 30px;
}

.category-item-container {
  scroll-snap-type: inline mandatory;
  overscroll-behavior-inline: contain;
  align-items: center;
  gap: 10px;
  display: flex;
  overflow-x: auto;
  overflow-y: hidden;
}

.category-item {
  min-width: 100%;
  border: 1px solid var(--cultured);
  border-radius: var(--border-radius-md);
  scroll-snap-align: start;
  align-items: center;
  gap: 10px;
  padding: 15px;
  display: flex;
}

.category-img-box {
  background: var(--cultured);
  border-radius: var(--border-radius-sm);
  border: 1px solid #ccc;
  padding: 10px;
}

.category-content-box {
  width: 100%;
}

.category-content-flex {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  display: flex;
}

.category-item-title {
  color: var(--eerie-black);
  font-size: var(--fs-9);
  font-weight: var(--weight-600);
  text-transform: uppercase;
}

.category-item-amount {
  color: var(--sonic-silver);
  font-size: var(--fs-11);
}

.category-btn {
  color: var(--salmon-pink);
  font-size: var(--fs-9);
  font-weight: var(--weight-500);
  text-transform: capitalize;
}

.sidebar {
  background: var(--white);
  width: 100%;
  max-width: 320px;
  overscroll-behavior: contain;
  visibility: hidden;
  z-index: 20;
  padding: 30px;
  transition: all .5s;
  position: fixed;
  top: 0;
  bottom: 0;
  left: -100%;
  overflow-y: scroll;
}

.sidebar.active {
  visibility: visible;
  left: 0;
}

.sidebar-category {
  border-bottom: 1px solid var(--cultured);
  margin-bottom: 15px;
  padding-bottom: 15px;
}

.sidebar-top {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  display: flex;
}

.sidebar-title {
  color: var(--onyx);
  font-size: var(--fs-5);
  text-transform: uppercase;
  letter-spacing: .8px;
  font-weight: var(--weight-600);
}

.sidebar-close-btn {
  color: var(--eerie-black);
  font-size: 22px;
  font-weight: var(--weight-600);
}

.sidebar-close-btn ion-icon {
  --ionicon-stroke-width: 50px;
}

.sidebar-accordion-menu {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 7px 0;
  display: flex;
}

.sidebar .menu-title-flex {
  align-items: center;
  gap: 10px;
  display: flex;
}

.sidebar .menu-title {
  font-size: var(--fs-5);
  color: var(--sonic-silver);
  font-weight: var(--weight-500);
}

.sidebar-accordion-menu ion-icon {
  color: var(--sonic-silver);
  --ionicon-stroke-width: 70px;
  font-size: 14px;
}

.sidebar-submenu-category-list {
  border-top: 1px solid var(--cultured);
  max-height: 0;
  visibility: hidden;
  transition: all .5s ease-in-out;
  overflow: hidden;
}

.sidebar-submenu-category-list.active {
  max-height: 122px;
  visibility: visible;
  padding: 13px 0 8px;
}

.sidebar-submenu-title {
  color: var(--sonic-silver);
  font-size: var(--fs-7);
  justify-content: space-between;
  align-items: center;
  padding: 2px 0;
  display: flex;
}

.sidebar-submenu-title:hover {
  color: var(--eerie-black);
}

.sidebar .product-name {
  text-transform: capitalize;
}

.sidebar-accordion-menu.active .add-icon, .sidebar-accordion-menu .remove-icon {
  display: none;
}

.sidebar-accordion-menu .add-icon, .sidebar-accordion-menu.active .remove-icon {
  display: block;
}

.sidebar .showcase-heading {
  font-size: var(--fs-5);
  font-weight: var(--weight-600);
  color: var(--onyx);
  text-transform: uppercase;
  letter-spacing: .8px;
  margin-bottom: 15px;
}

.sidebar .showcase {
  align-items: center;
  gap: 15px;
  display: flex;
}

.sidebar .showcase:not(:last-child) {
  margin-bottom: 15px;
}

.sidebar .showcase-img {
  border-radius: var(--border-radius-sm);
}

.sidebar .showcase-content {
  width: calc(100% - 90px);
}

.sidebar .showcase-title {
  color: var(--onyx);
  font-size: var(--fs-7);
  font-weight: var(--weight-400);
  text-transform: capitalize;
  white-space: nowrap;
  text-overflow: ellipsis;
  letter-spacing: .5px;
  overflow: hidden;
}

.sidebar .showcase-rating {
  color: var(--sandy-brown);
  align-items: center;
  padding: 4px 0;
  font-size: 13px;
  display: flex;
}

.sidebar .price-box {
  align-items: center;
  gap: 15px;
  display: flex;
}

.sidebar .price-box del {
  color: var(--sonic-silver);
  font-size: 13px;
}

.sidebar .price-box .price {
  font-size: var(--fs-7);
  font-weight: var(--weight-600);
  color: var(--davys-gray);
}

.product-minimal {
  margin-bottom: 30px;
}

.product-minimal .product-showcase {
  margin-bottom: 10px;
}

.product-minimal .showcase-wrapper {
  overscroll-behavior-inline: contain;
  scroll-snap-type: inline mandatory;
  align-items: center;
  display: flex;
  overflow-x: auto;
}

.product-minimal .showcase-container {
  min-width: 100%;
  scroll-snap-align: start;
  padding: 0 5px;
}

.product-minimal .showcase {
  border: 1px solid var(--cultured);
  border-radius: var(--border-radius-md);
  justify-content: flex-start;
  align-items: center;
  gap: 15px;
  padding: 15px;
  display: flex;
}

.product-minimal .showcase:not(:last-child) {
  margin-bottom: 15px;
}

.product-minimal .showcase-content {
  width: calc(100% - 85px);
}

.product-minimal .showcase-title {
  color: var(--eerie-black);
  font-size: var(--fs-7);
  font-weight: var(--weight-600);
  text-transform: capitalize;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-bottom: 2px;
  overflow: hidden;
}

.product-minimal .showcase-category {
  width: max-content;
  color: var(--davys-gray);
  font-size: var(--fs-8);
  text-transform: capitalize;
  margin-bottom: 3px;
}

.product-minimal .showcase-category:hover {
  color: var(--salmon-pink);
}

.product-minimal .price-box {
  align-items: center;
  gap: 10px;
  display: flex;
}

.product-minimal .price {
  font-size: var(--fs-7);
  font-weight: var(--weight-700);
  color: var(--salmon-pink);
}

.product-minimal .price-box del {
  font-size: var(--fs-9);
  color: var(--sonic-silver);
}

.product-featured {
  margin-bottom: 30px;
}

.product-featured .showcase-wrapper {
  overscroll-behavior-inline: contain;
  scroll-snap-type: inline mandatory;
  align-items: center;
  gap: 20px;
  display: flex;
  overflow-x: auto;
}

.product-featured .showcase-container {
  min-width: 100%;
  border: 1px solid var(--cultured);
  border-radius: var(--border-radius-md);
  scroll-snap-align: start;
  padding: 30px;
}

.product-featured .showcase-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.product-featured .showcase-content {
  margin-top: 30px;
}

.product-featured .showcase-rating {
  color: var(--sandy-brown);
  align-items: center;
  margin-bottom: 15px;
  font-size: 16px;
  display: flex;
}

.product-featured .showcase-title {
  font-size: var(--fs-7);
  color: var(--eerie-black);
  white-space: nowrap;
  text-overflow: ellipsis;
